<script>
import Head from "./../header/Head";
import Foot from "./../footer/Foot";
export default {
  components: {
    Head,
    Foot
  },
  data() {
    return {
      id: "登陆 / 注册",
      useimg: "",
      usebol: true
    };
  },
  methods: {
    //点击登陆,并且判断是否已经登陆,如果已经成功登陆则跳转个人信息页,如果没登陆则跳转到登陆页面
    getrouter() {
      if (this.usebol) {
        this.$router.push({ path: "/Register" });
      } else {
        this.$router.push({ path: "/Registerok" });
      }
    }
  },
  mounted() {
      if (this.$store.state.users == []) {
        this.id = "登陆 / 注册";
        this.usebol = true;
      } else {
          this.id = this.$store.state.users[0].name;
          this.useimg = this.$store.state.users[0].avatar;
          this.usebol = false;
        }
  }
};
</script>

